import 'reset-css';
import '../style.scss';
import '../javascripts/my.js';

import AOS from 'aos';
import 'aos/dist/aos.css';
AOS.init();

// import 'swiper/dist/css/swiper.css';
// import Swiper from 'swiper';
var mySwiper = new Swiper('.swiper-container',{
// 以下にオプションを設定
loop: true, //最後に達したら先頭に戻る
centeredSlides : true,  //アクティブなスライドを中央に表示
autoplay: {
delay: 5000,
// autoHeight: true,
// setWrapperSize: true,
},
// レスポンシブブレーポイント（画面幅による設定）
breakpoints: {
// 画面幅が 640px 以上の場合（window width >= 640px）
375: {
  slidesPerView: 1,
  // loopedSlides: 3,
  // spaceBetween: 30,
},
768: {
  slidesPerView: 2,
  loopedSlides: 3,
  spaceBetween: 30,
},
// 画面幅が 980px 以上の場合（window width >= 980px）
1022: {
  slidesPerView: 2,
  loopedSlides: 3,
  spaceBetween: 56,
},
},
});







